import { useRef, useState } from "react";
import "./styles.css";

function padTime(time) {
  // pads the current string with another string
  return time.toString().padStart(2, '0'); // 00
}

export default function App() {
const [title, setTitle] = useState('Lets count down begin! ‚åõÔ∏è  ')
const [timeInFinishing, setTimeLeft] = useState(25 * 60);
const intervalRef = useRef(0);

function startTimer() {
  setTitle(`Be Focused, You are almost there! üë©üèΩ‚Äçüíª`)
 intervalRef.current = setInterval(() => {
  setTimeLeft(timeInFinishing =>
    {
      if(timeInFinishing > 1 ) return timeInFinishing -1
    
      else return 0;
    });
  }, 1000);
}
function stopTimer() {
  setTitle(`Take some break and be back! ü•Ç `);
  clearInterval(intervalRef.current)
}

function resetTimer() {
  setTitle(`Wanna go for another round? ‚è≥`);
  clearInterval(intervalRef.current)
  setTimeLeft(25 * 60);

}
 const minutes = padTime(Math.floor(timeInFinishing / 60));
 const seconds = padTime(timeInFinishing - minutes * 60);


  return (
    <div className="app">
      <h2>{title}</h2>

      <div className="timer">
        <span>{minutes}</span>
        <span>:</span>
        <span>{seconds}</span>
      </div>

      <div className="buttons">
        <button onClick={startTimer}>Start</button>
        <button onClick={stopTimer}>Stop</button>
        <button onClick={resetTimer} >Reset</button>
      </div>
    </div>
  );
}
